<template>
    <my-page class="page-home" :title="title" :page="page">
        <div id="main">
            <ul class="book-list">
                <li class="item" v-for="book in books">
                    <router-link class="link" :to="'/books/' + book.id" :title="book.name">
                        <!-- <a class="remove" href="#" @click.stop.prevent="download(book)">下载</a> -->
                        <img class="cover" :src="book.cover" v-if="book.cover" />
                        <div class="cover-text" v-if="!book.cover">没有封面</div>
                    </router-link>
                    <div class="name">{{ book.name }}</div>
                    <a class="remove" href="#" @click.stop.prevent="remove(book)">删除</a>
                    <!-- <div class="author">{{ book.author }}</div> -->
                </li>
            </ul>
            <div class="empty-box" v-if="!books.length">
                <img src="/static/img/empty.svg"/>
                <div class="text">暂无书籍，你可以添加右上角的“+”添加书籍</div>
            </div>
            <ui-raised-button class="file-btn" label="从文件中导入" style="display: none">
                <input type="file" id="file" class="ui-file-button" @change="fileChange($event, 1)">
            </ui-raised-button>
        </div>
    </my-page>
</template>

<script>
    /* eslint-disable2 */
    import bookDb from '../util/bookDb2'
    import reader from '../util/reader'
    import {getCoverURL} from '../util/bookUtil'
    const ePub = window.ePub

    export default {
        data () {
            return {
                reader: null,
                title: 'Acerkaio 的书架',
                books: [
                    {
                        id: '1',
                        name: '呵呵哒',
                        author: '陈建行',
                        content: '123'
                    },
                    {
                        id: '2',
                        name: '致我们即将逝去的青春',
                        author: '陈建行',
                        content: '123'
                    }
                ],
                page: {
                    menu: [
                        {
                            type: 'icon',
                            icon: 'add',
                            click: this.add,
                            title: '添加本地书籍'
                        },
                        {
                            type: 'icon',
                            icon: 'cloud_download',
                            click: this.addLink,
                            title: '添加云端书籍'
                        },
                        {
                            type: 'icon',
                            icon: 'apps',
                            href: 'https://blog.acerkaio.top/',
                            target: '_blank',
                            title: 'Acerkaio'
                        }
                    ]
                }
            }
        },
        mounted() {
            this.init()
        },
        destroyed() {
        },
        methods: {
            add() {
                document.getElementById('file').click()
            },
            init() {
                this.reader = reader
                console.log('初始化')
                bookDb.init(() => {
                    bookDb.getBooks(data => {
                        console.log('获取所有书籍')
                        console.log(data)
                        this.books = data
                        // 添加示例图书
                        let isInit = this.$storage.get('init', false)
                        if (!isInit) {
                            this.$storage.set('init', true)
                            // this.addBookFromUrl('https://img1.yunser.com/epub/test.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/安达与岛村/01安达与岛村.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/安达与岛村/02安达与岛村.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/安达与岛村/03安达与岛村.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/安达与岛村/04安达与岛村.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/安达与岛村/05安达与岛村.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/安达与岛村/06安达与岛村.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/安达与岛村/07安达与岛村.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/安达与岛村/08安达与岛村.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/安达与岛村/09安达与岛村.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/安达与岛村/10安达与岛村.epub')
                            this.addBookFromUrl(
                              'https://data.acerkaio.top/%E5%AE%89%E8%BE%BE%E4%B8%8E%E5%B2%9B%E6%9D%91/%E7%9F%AD%E7%AF%87%20%E5%AE%89%E8%BE%BE%E4%B8%8E%E5%B2%9B%E6%9D%91.epub'
                            )
                            this.addBookFromUrl(
                              'https://data.acerkaio.top/转生王和天才千金的魔法革命/01转生王和天才千金的魔法革命.epub'
                            )
                            this.addBookFromUrl(
                              'https://data.acerkaio.top/转生王和天才千金的魔法革命/02转生王和天才千金的魔法革命.epub'
                            )
                            this.addBookFromUrl(
                              'https://data.acerkaio.top/转生王和天才千金的魔法革命/03转生王和天才千金的魔法革命.epub'
                            )
                            this.addBookFromUrl(
                              'https://data.acerkaio.top/转生王和天才千金的魔法革命/04转生王和天才千金的魔法革命.epub'
                            )
                            this.addBookFromUrl(
                              'https://data.acerkaio.top/转生王和天才千金的魔法革命/05转生公主与天才千金的魔法革命.epub'
                            )
                            this.addBookFromUrl(
                              'https://data.acerkaio.top/%E8%BD%AC%E7%94%9F%E7%8E%8B%E5%92%8C%E5%A4%A9%E6%89%8D%E5%8D%83%E9%87%91%E7%9A%84%E9%AD%94%E6%B3%95%E9%9D%A9%E5%91%BD/-%E7%AF%87%20%E8%BD%AC%E7%94%9F%E7%8E%8B%E5%92%8C%E5%A4%A9%E6%89%8D%E5%8D%83%E9%87%91%E7%9A%84%E9%AD%94%E6%B3%95%E9%9D%A9%E5%91%BD.epub'
                            )
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 - 01-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 - 02-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 - 03-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 - 04-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 - 05-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 - 06-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 - 07-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 - 08-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 - 09-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 - 10-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 - 11-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 - 12-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 - 13-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 - 14-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 - 15-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 - 16-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 - 17-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 - 18-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 - 19-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 - 20-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 短篇 - 01-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔女之旅 番外 - 01-tc.epub')
                            this.addBookFromUrl('https://data.acerkaio.top/maj/魔之旅 - 20.epub')
                        }
                    })
                })
            },
            addLink() {
                let link = prompt('请输入链接')
                console.log(link)
                this.addBookFromUrl(link)
            },
            addBookFromUrl(url) {
                this.book = ePub(url, {
                    width: 400,
                    height: 600,
                    spreads: false,
                    restore: true
                })
                this.book.getMetadata().then(meta => {
                    console.log('getMetadata')
                    console.log(meta)
                    bookDb.init(() => {
                        bookDb.addBook({
                            type: 'link',
                            id: '' + new Date().getTime(),
                            name: meta.bookTitle,
                            author: meta.creator,
                            content: url
                        }, () => {
                            bookDb.getBooks(data => {
                                console.log('获取所有书籍')
                                this.books = data
                                console.log(data)
                            })
                        })
                    })
                })
            },
            remove(book) {
                bookDb.init(() => {
                    bookDb.deleteBook(book.id, data => {
                        bookDb.getBooks(data => {
                            console.log('获取所有书籍')
                            console.log(data)
                            this.books = data
                        })
                    })
                })
            },
            download(book) {
                let fileName = 'dl.epub'
                let blob = book.content
                if (window.navigator.msSaveOrOpenBlob) {
                    navigator.msSaveBlob(blob, fileName)
                } else {
                    var link = document.createElement('a')
                    link.href = window.URL.createObjectURL(blob)
                    link.download = fileName
                    link.click()
                    window.URL.revokeObjectURL(link.href)
                }
            },
            fileChange(e) {
                let files = e.target.files
                if (!files.length) {
                    return
                }
                let file = e.target.files[0]
//                if (left === 1) {
//                    var f_name = file.name
//                    var f_type = f_name.substring(f_name.lastIndexOf("."))
//                }
                console.log(file.name)
                let reader = new FileReader()
                reader.onload = e => {
                    let content = e.target.result
                    this.book = ePub({
                        bookPath: content,
                        restore: false
                    })
                    this.book.getMetadata().then(meta => {
                        this.meta = meta
                        console.log('getMetadata')
                        console.log(meta)
                        getCoverURL(this.book, cover => {
                            console.log('首页封面')
                            console.log(cover)
                            bookDb.init(() => {
                                bookDb.addBook({
                                    id: '' + new Date().getTime(),
                                    name: meta.bookTitle,
                                    author: meta.creator,
                                    content: content,
                                    cover: cover
                                }, () => {
                                    bookDb.getBooks(data => {
                                        console.log('获取所有书籍')
                                        this.books = data
                                        console.log(data)
                                    })
                                })
                            })
                        })
                    })
                }
                reader.readAsArrayBuffer(file)
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../scss/var";

    @keyframes rotate {
        to { transform: rotate(360deg); }
    }

    .book-list{
        @include clearfix;
        .item {
            position: relative;
            float: left;
            width: 140px;
            height: 196px;
            margin-right: 16px;
            margin-bottom: 76px;
            background-color: #fff;
            /*background-image: url("/static/img/bg_1.jpg");*/
            box-shadow: 0 1px 6px rgba(0,0,0,.117647), 0 1px 4px rgba(0,0,0,.117647);
            &:hover {
                // .remove {
                //     display: block;
                // }
            }
        }
        .link {
            display: block;
            height: 100%;
            // padding: 16px;
            margin-bottom: 16px;
            color: inherit;
        }
        .name {
            // font-weight: bold;
        }
        .cover {
            max-width: 100%;
        }
        .cover-text {
            line-height: 196px;
            text-align: center;
            color: #999;
        }
        .author {
            position: absolute;
            bottom: 16px;
            left: 16px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
        .remove {
            display: none;
        }
    }
    .empty-box {
        width: 480px;
        margin: 80px auto;
        padding: 32px;
        text-align: center;
        background-color: #fff;
        box-shadow: 0 0.5px 1px 0 rgba(0, 0, 0, .12);
        img {
            margin-bottom: 16px;
            width: 100px;
            animation: rotate 5s infinite linear;
        }
        .text {
            color: #999;
        }
    }
</style>



